<template>
    <div class="user-wrap">
        <h1 class="wrap-title mar-bottom15"></h1>
        <el-row class="mar-bottom10">
            <el-col :span="24" class="el-item pdd-10">
                <el-table :data="list" style="width: 100%">
                    <el-table-column prop="id" label="#" width="70"></el-table-column>
                    <el-table-column prop="name" label="" width="100"></el-table-column>
                    <el-table-column prop="phone" label="" width="140"></el-table-column>
                    <el-table-column prop="email" label="" width="180" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="password" label="" width="130"></el-table-column>
                    <el-table-column prop="mark" label="" show-overflow-tooltip></el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <el-row class="mar-bottom10">
            <el-col :span="24" class="el-item">
                <div class="form-title"></div>
                <el-form ref="form" :model="form" label-width="80px" class="content-form">
                    <el-form-item label="">
                        <el-input v-model="form.a"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="form.b" placeholder="" filterable>
                            <el-option label="" :value="0"></el-option>
                            <el-option label="" :value="1"></el-option>
                            <el-option label="" :value="2"></el-option>
                            <el-option label="" :value="3"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.c"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.d"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.e"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.f"></el-input>
                    </el-form-item>
                    <el-form-item label=" ">
                        <el-button type="primary" size="small"></el-button>
                        <el-button type="success" size="small"></el-button>
                        <el-button type="danger" size="small"></el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    data () {
        return {
            form: {
                a: '',
                b: 0,
                c: '',
                d: '',
                e: '',
                f: ''
            },
            list: [
                {
                    id: 1001,
                    name: '',
                    phone: 13488717674,
                    email: 'licongsheng@163.com',
                    password: '123456',
                    mark: ''
                }
            ]
        }
    }
}
</script>



