<template>
    <div>
        <h1 class="wrap-title mar-bottom15"></h1>
        <el-row class="mar-bottom10">
            <el-col :span="24" class="el-item pdd-10">
                <el-table :data="list" style="width: 100%">
                    <el-table-column prop="a" label="#" width="70"></el-table-column>
                    <el-table-column prop="b" label="" width="150" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="c" label="" width="140"></el-table-column>
                    <el-table-column prop="d" label="" width="100" ></el-table-column>
                    <el-table-column prop="e" label="" width="100"></el-table-column>
                    <el-table-column prop="f" label="" width="130"></el-table-column>
                    <el-table-column prop="g" label=""></el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <el-row class="mar-bottom10">
            <el-col :span="24" class="el-item">
                <div class="form-title"></div>
                <el-form ref="form" :model="form" label-width="95px" class="content-form">
                    <el-form-item label="">
                        <el-input v-model="form.a"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.b"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.c"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.d"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.e"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                            <el-select v-model="form.f" placeholder="" filterable>
                                <el-option label="" :value="0"></el-option>
                                <el-option label="" :value="1"></el-option>
                            </el-select>
                    </el-form-item>
                    <el-form-item label=" ">
                        <el-button type="primary" size="small"></el-button><br>
                        <el-button type="success" size="small"></el-button><br>
                        <el-button type="danger" size="small"></el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    data () {
        return {
            form: {
                a: '',
                b: '',
                c: '',
                d: '',
                e: '',
                f: 0
            },
            list: [
                {
                    a: 1001,
                    b: '',
                    c: '',
                    d: 'licongsheng@163.com',
                    e: '123456',
                    f: 0,
                    g: 'jffjfj'
                }
            ]
        }
    }
}
</script>



